/* About me section styling */
section {
    padding : 80px 0;
}
.aboutP{
    font-family: "Raleway-bold";
    font-size: 17px;
}

.about-me-section{
    font-family:  Montserrat;
   /* margin-bottom: -20px; */
    background: #f8e4e1;
    height: 100vh;
}
.title1{
    position: relative;
    text-align: center;
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 60px;
    padding-bottom: 20px;
    
}
.title1::before{
    content: "";
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 205px;
    height: 4px;
    background: black;
    transform: translateX(-50%);
    
}
.title1::after{
    content: "who i am";
    position: absolute;
    bottom: -12px;
    left: 50%;
    font-size: 20px;
    color:#92451b;
    font-weight: 900;
    padding:5px;
    background:#f8e4e1;
    
    transform: translateX(-50%);
    
}
.about-content, .projects-content, .skills .skills-content,.contact .contact-content{
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}.left{
    width:45%;
}
.left img{
    height :400px;
    width: 400px;
    object-fit: cover;
   
    border-radius: 6px;
}
.right{
    width: 55%;
}
